.App {
  text-align: center;
}

.App-header {
  background-image: url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQLllgBkj15POsu0DOJGfxtzuiQWc1aJ3kzuw&usqp=CAU);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Navbar ul {
  display: flex;
  gap: 30px;
  list-style-type: none;
}

.Navbar ul li a {
  text-decoration: none;
  color: white;
}

.Navbar ul li a:hover {
  color: aquamarine;
}

.Movie-container {
  width: 100%;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
  margin-top: 20px;
}

.Movie-wrapper {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  width: 500px;
  color: #fafafa;
  border-radius: 10px;
  padding: 15px;
}

.Movie-search {
  height: 40px;
  font-size: .9rem;
  margin-bottom: 20px;
  border-radius: 50px;
  padding-left: 20px;
  width: 50%;
  background: transparent;
  color: white;
  border: 2px solid white;
  box-shadow: none;
}

.Movie-title {
  height: 60px;
  margin-top: 10px;
  font-weight: bold;
}

.Movie-date {
  font-weight: 600;
  margin-top: 10px;
}

.Movie-rate {
  color: rgb(255, 230, 5);
  font-weight: 900;
  margin-bottom: 10px;
  margin-top: 10px;
}

.Movie-image {
  border-radius: 10px;
}

.Button {
  margin-bottom: 20px;
  margin-top: 20px;
}

.Button a {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
  background: black;
  padding: 15px;
  border-radius: 50px;
}

.Button a:hover {
  background: transparent;
  border: 2px solid white;
  border-radius: 50px;
}

.Banner-movie img {
  border-radius: 10px;
  border: 2px solid rgb(70, 250, 211);
}

footer {
  margin-top: 20px;
}
